import React from 'react';
import { Row, Col, Card } from 'antd';
import GGEditor, {
  Flow,
  Command,
  Minimap,
  Toolbar,
  Item,
  ItemPannel,
  NodePannel,
  EdgePannel,
  GroupPannel,
  MultiPannel,
  CanvasPannel,
  DetailPannel,
} from '@src';
import NodeDetail from '../../components/NodeDetail';
import styles from './index.scss';
import iconfont from '../../theme/iconfont.scss';

class FlowPage extends React.Component {
  flow = null;

  renderFlow() {
    return (
      <Flow
        ref={(component) => {
          if (component) {
            this.flow = component.page;
          }
        }}
        className={styles.flow}
      />
    );
  }

  renderToolbar() {
    return (
      <Toolbar className={styles.toolbar}>
        <Command name="undo">
          <i className={`${iconfont.iconfont} ${iconfont.iconUndo}`} title="" />
        </Command>
        <Command name="redo">
          <i className={`${iconfont.iconfont} ${iconfont.iconRedo}`} title="" />
        </Command>

        <Command name="copy">
          <i className={`${iconfont.iconfont} ${iconfont.iconCopyO}`} title="" />
        </Command>
        <Command name="paste">
          <i className={`${iconfont.iconfont} ${iconfont.iconPasterO}`} title="" />
        </Command>
        <Command name="delete">
          <i className={`${iconfont.iconfont} ${iconfont.iconDeleteO}`} title="" />
        </Command>

        <Command name="zoomIn">
          <i className={`${iconfont.iconfont} ${iconfont.iconZoomInO}`} title="" />
        </Command>
        <Command name="zoomOut">
          <i className={`${iconfont.iconfont} ${iconfont.iconZoomOutO}`} title="" />
        </Command>
        <Command name="autoZoom">
          <i className={`${iconfont.iconfont} ${iconfont.iconFit}`} title="" />
        </Command>
        <Command name="resetZoom">
          <i className={`${iconfont.iconfont} ${iconfont.iconActualSizeO}`} title="" />
        </Command>

        <Command name="toBack">
          <i className={`${iconfont.iconfont} ${iconfont.iconToBack}`} title="" />
        </Command>
        <Command name="toFront">
          <i className={`${iconfont.iconfont} ${iconfont.iconToFront}`} title="" />
        </Command>

        <Command name="multiSelect">
          <i className={`${iconfont.iconfont} ${iconfont.iconSelect}`} title="" />
        </Command>
        <Command name="addGroup">
          <i className={`${iconfont.iconfont} ${iconfont.iconGroup}`} title="" />
        </Command>
        <Command name="unGroup">
          <i className={`${iconfont.iconfont} ${iconfont.iconUngroup}`} title="" />
        </Command>
      </Toolbar>
    );
  }

  renderItemPannel() {
    return (
      <ItemPannel className={styles.itemPannel}>
        <Card bordered={false}>
          <Item
            type="node"
            size="72*72"
            shape="flow-circle"
            model={{
              color: '#FA8C16',
              label: '',
            }}
            src="https://gw.alipayobjects.com/zos/rmsportal/ZnPxbVjKYADMYxkTQXRi.svg"
          />
          <Item
            type="node"
            size="80*48"
            shape="flow-rect"
            model={{
              color: '#1890FF',
              label: '',
            }}
            src="https://gw.alipayobjects.com/zos/rmsportal/wHcJakkCXDrUUlNkNzSy.svg"
          />
          <Item
            type="node"
            size="80*72"
            shape="flow-rhombus"
            model={{
              color: '#13C2C2',
              label: '',
            }}
            src="https://gw.alipayobjects.com/zos/rmsportal/SnWIktArriZRWdGCnGfK.svg"
          />
          <Item
            type="node"
            size="80*48"
            shape="flow-capsule"
            model={{
              color: '#722ED1',
              label: '',
            }}
            src="https://gw.alipayobjects.com/zos/rmsportal/rQMUhHHSqwYsPwjXxcfP.svg"
          />
        </Card>
      </ItemPannel>
    );
  }

  renderDetailPannel() {
    return (
      <DetailPannel className={styles.detailPannel}>
        <NodePannel>
          <NodeDetail />
        </NodePannel>
        <EdgePannel>
          <Card type="inner" title="" bordered={false} />
        </EdgePannel>
        <GroupPannel>
          <Card type="inner" title="" bordered={false} />
        </GroupPannel>
        <MultiPannel>
          <Card type="inner" title="" bordered={false} />
        </MultiPannel>
        <CanvasPannel>
          <Card type="inner" title="" bordered={false} />
        </CanvasPannel>
      </DetailPannel>
    );
  }

  renderMinimap() {
    return (
      <Card type="inner" title="" bordered={false}>
        <Minimap height={200} />
      </Card>
    );
  }

  render() {
    return (
      <GGEditor className={styles.editor}>
        <Row type="flex" className={styles.editorHd}>
          <Col span={24}>
            {this.renderToolbar()}
          </Col>
        </Row>
        <Row type="flex" className={styles.editorBd}>
          <Col span={4} className={styles.editorSidebar}>
            {this.renderItemPannel()}
          </Col>
          <Col span={16} className={styles.editorContent}>
            {this.renderFlow()}
          </Col>
          <Col span={4} className={styles.editorSidebar}>
            {this.renderDetailPannel()}
            {this.renderMinimap()}
          </Col>
        </Row>
      </GGEditor>
    );
  }
}

export default FlowPage;



